import { defineComponent, computed, ref } from "vue";
import BaseBackLayout from "@/components/Common/BaseBackLayout.vue";
import BaseBtn from "@/components/SellCar/BaseBtn.vue";
import { IonTextarea } from "@ionic/vue";
import useSellCarFilter from "@/hooks/sell/useSellCarFilter";
import useSellCarContact from "@/hooks/sell/useSellCarContact";
import ListItem from "@/components/Common/ListItem.vue";
import SellCarPostManager from "@/functional/SellCarPostManager.vue";
import SellCarContactManager from "@/functional/SellCarContactManager.vue";
import router from "@/router";
import useSellCarPost from "@/hooks/sell/useSellCarPost";
import { showLoading, closeLoading } from "@/hooks/useLoading";
import { maxValue, minValue, required, email } from "@vuelidate/validators";
export default defineComponent({
  components: {
    BaseBackLayout,
    IonTextarea,
    BaseBtn,
    ListItem,
    SellCarPostManager,
    SellCarContactManager
  },
  setup() {
    const {
      isNullCarSellFilters
    } = useSellCarFilter();
    const {
      createPost,
      isNullCarSellPostByCate,
      returnFormInvalidity
    } = useSellCarPost();
    const {
      isNullCarSellContactByCate,
      returnContactInvalidity
    } = useSellCarContact();
    const registerTemp = ref(true);
    let disabled = computed(() => {
      return isNullCarSellPostByCate("Expired Date") && registerTemp.value || isNullCarSellPostByCate("location") || isNullCarSellPostByCate("transmission") || isNullCarSellPostByCate("built_year") || isNullCarSellPostByCate("Listing Price") || isNullCarSellPostByCate("Exterior Colour") || isNullCarSellContactByCate("lastname") || isNullCarSellContactByCate("firstname") || isNullCarSellContactByCate("email") || isNullCarSellContactByCate("phoneNumber") || returnFormInvalidity() || returnContactInvalidity();
    });
    let process = async () => {
      try {
        showLoading();
        const {
          success,
          message
        } = await createPost();
        if (success) {
          router.push("/tabs/sell-car/ad-select");
        }
      } catch (err) {
        console.log(err);
      }
      closeLoading();
    };
    const valBuiltYear = computed(() => ({
      value: {
        required,
        minValue: minValue(1900),
        maxValue: maxValue(2100)
      }
    }));
    const valExpiredDate = computed(() => {
      return {
        inputMonth: {
          minValue: minValue(0),
          maxValue: maxValue(12)
        },
        inputYear: {
          minValue: minValue(1990),
          maxValue: maxValue(2100)
        }
      };
    });
    const valEmail = computed(() => ({
      value: {
        required,
        email
      }
    }));
    return {
      process,
      disabled,
      registerTemp,
      valBuiltYear,
      valExpiredDate,
      valEmail
    };
  }
});